@import url(https://fonts.googleapis.com/css2?family=Charis+SIL:ital@1&family=Open+Sans:ital,wght@0,400;1,300&display=swap);
*{
    margin: 0;
    padding: 0;
    text-decoration: none;
    box-sizing: border-box;
    font-family: 'Gentium Plus', serif;
}

.navbar{
    background-color: #fff !important;
}

.navbar-brand{
    font-size: 2rem !important;
    color: #565387 !important;
}

.navbar a{
    font-size: 1.3rem;
    text-transform: capitalize;
}

.menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387;
}

.navbar a:hover{
    color: #3498db !important;
}

#mynavbar ul li a{
    color: #000;
}
#header{
    width: 100%;
    height: 80vh;
    margin-top: 70px;
}
#header h1 {
    margin: 0 0 10px 0 ;
    font-size: 48px;
    font-weight: 700;
    color: #000;
}

.navbar-toggler span{
    color: #000 !important;
}

#header .brand-name{
    color: #3498db;
}

#header h2 {
    color: #484848;
    font-size: 24px;
}

#header .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30 px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
}

#header .btn-get-started:hover{
    background: #3498db;
    color: #fff;
}

.header-img{
    text-align: center;
}

.header-img img {
  width: 60%;  
}

#header .animated{
    animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down{
    0%{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

@keyframes up-down{
    0%{
        -webkit-transform: translateY(10px);
                transform: translateY(10px);
    }
    100%{
        -webkit-transform: translateY(-10px);
                transform: translateY(-10px);
    }
}

